.searchbar {
    margin-left: 200px;
    margin-right: 200px;

    top: 0;
    left: 0;
    position: sticky;
    z-index: 1100;
    display: flex;
    justify-content: center;
    align-items: center;
    // min-height: 64px;
    // margin-top: 20px;
    // padding-right: 24px;
    // padding-left: 24px;
    padding-top: 14px;
    padding-bottom: 14px;
    color: #fff;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
}

.SearchFormButton {
    display: inline-block;
    width: 28px;
    height: 28px;
    border: 0;
    background-image: url('https://img.icons8.com/metro/52/000000/search.png');
    background-size: 50%;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.SearchFormButton:hover {
    opacity: 1;
}

.SearchFormButtonLabel {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
}

.SearchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 16px;
    border: none;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
}

.SearchFormInput::placeholder {
    font: inherit;
    font-size: 18px;
}